




























import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import HotelDatePicker  from '@/components/forms/hotel-date-picker.vue';
import BaseCheckbox     from '@/components/forms/base-checkbox.vue';
import {i_checkinPeriod, i_select} from "@/core/declarations";
import {convertToDateFormat}   from '@/core/helpers.ts';
import {Route} from "vue-router";
import {RequestError} from "visbook-api";
import store from "@/store";
interface i_filterParams {
  checkinPeriod: i_checkinPeriod
  adults: string,
  child: string
}
interface i_query {
  checkIn?: string
  checkOut?: string
  lang?: string
  webProductId?: string
}

@Component({
  components: {HotelDatePicker, BaseCheckbox}
})
export default class SearchFilter extends Vue {
  $store:any=store;
  created() {
    const query:i_query = this.$route.query;
    if(query.checkIn)  this.filterParams.checkinPeriod.checkIn = new Date(query.checkIn);
    if(query.checkOut) this.filterParams.checkinPeriod.checkOut = new Date(query.checkOut);
    this.searchWebProducts(false);
  }

  filterParams:i_filterParams = {
    checkinPeriod: {
      checkIn: null,
      checkOut: null
    },
    adults: '1',
    child: '0'
  };
  get selectedRoomType():i_select[] {
    return this.$store.state.mod_search.filterSettingsDynamic.roomType;
  }
  get numberOfPersons() {
    return this.filterParams.adults + this.filterParams.child
  }

  searchWebProducts(updateRoute:boolean) {
    const from = convertToDateFormat(this.filterParams.checkinPeriod.checkIn);
    const to = convertToDateFormat(this.filterParams.checkinPeriod.checkOut);
    if(!from || !to) return;

    this.$store.dispatch('mod_search/SET_filterSettings', this.filterParams);

    this.$store.dispatch('mod_search/GET_webProductsList', {
      from, to,
      numberOfPersons: this.numberOfPersons,
      pensionType: 0,
    });
    if(updateRoute) {
      const query: { [key: string]: string } = {
        checkIn: from,
        checkOut: to,
      };
      if (this.selectedRoomType.length) {
        const webProductList = this.selectedRoomType.map((el) => el.value);
        query.webProductId = JSON.stringify(webProductList);
      }
      const route = this.$route;
      console.log(query);
      this.$router.push({
        path: route.path,
        query: {
          ...route.query,
          ...query
        }
      });
    }
  }
}
