


















import { Component, Vue, Watch } from 'vue-property-decorator';
import TemplateBase     from "@/pages/templates/template-base.vue";
import LayoutBase       from "@/pages/templates/layout-base.vue";
import ThePagination    from "@/components/the-pagination/the-pagination.vue";
import SearchFilter       from "@/components/page-search/search-filter.vue";
import SearchResultRoom   from "@/components/page-search/search-result-room.vue";
import SearchResultEvent  from "@/components/page-search/search-result-event.vue";
import store from "@/store";

@Component({
  components: {LayoutBase, TemplateBase, ThePagination, SearchFilter, SearchResultRoom, SearchResultEvent}
})
export default class PageSearch extends Vue {
  $store:any=store;
  get resultList():any[] {
    return this.$store.getters['mod_search/GET_filteredRooms']
  }
}
