

























import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import BaseCheckbox     from '@/components/forms/base-checkbox.vue';
import DetailsInfo      from "@/components/different/details-info.vue";
import PriceCurrency      from "@/components/different/price-currency.vue";


@Component({
  components: {BaseCheckbox, DetailsInfo, PriceCurrency}
})
export default class SearchResultRoomAdditional extends Vue {
  @Prop({type: Object, default: ()=> null}) itemAdd!: any;
  @Prop({type: Number, default: 1}) allNights!: number;
  @Prop({type: String, default: ''}) itemCurrency!: string;

  isAdd = false;
  quantity = 1;

  get totalPrice() {
    return this.itemAdd.serviceType === 'perDay'
      ? +this.itemAdd.price * +this.quantity * +this.allNights
      : +this.itemAdd.price
  }

  @Watch('isAdd')
  @Watch('quantity')
  updateAddData() {
    const result = {
      id: this.itemAdd.id,
      encryptedCompanyId: this.itemAdd.encryptedCompanyId,
      count: this.quantity,
      totalPrice: this.totalPrice //NOT FOR REQUEST
    };
    this.isAdd
      ? this.$emit('add-data', result)
      : this.$emit('delete-data', result)
  }
}
