




















































import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import HotelDatePicker  from '@/components/forms/hotel-date-picker.vue';
import BaseCheckbox     from '@/components/forms/base-checkbox.vue';
import DetailsInfo      from "@/components/different/details-info.vue";
import {i_checkinPeriod, i_select} from "@/core/declarations";
import {convertToDateFormat}   from '@/core/helpers.ts';
import {Route} from "vue-router";
import {RequestError} from "visbook-api";

interface i_filterParams {
  checkinPeriod: i_checkinPeriod
  webProductIdSelected: i_select[]
  adults: string,
  child: string
}
interface i_query {
  checkIn?: string
  checkOut?: string
  lang?: string
  webProductId?: string
}

@Component({
  components: {HotelDatePicker, BaseCheckbox, DetailsInfo}
})
export default class SearchResultEvent extends Vue {
  @Prop({type: Object, default: ()=> null}) itemData!: any;

  swiperOption = {
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  };
  showInfo = false;
  toggleInfo() {
    this.showInfo = !this.showInfo
  }
}
