




























































































import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import HotelDatePicker  from '@/components/forms/hotel-date-picker.vue';
import SearchResultRoomAdditional     from '@/components/page-search/search-result-room-additional.vue';
import PriceCurrency      from "@/components/different/price-currency.vue";
import DetailsInfo      from "@/components/different/details-info.vue";
import {i_checkinPeriod, i_select} from "@/core/declarations";
import {convertToDateFormat}   from '@/core/helpers.ts';
import {Route} from "vue-router";
import {RequestError, in_reservations} from "visbook-api";
import {i_cartItem, i_filterSettings} from "@/store/store";
import store from "@/store";
interface i_filterParams {
  checkinPeriod: i_checkinPeriod
  webProductIdSelected: i_select[]
  adults: string,
  child: string
}
interface i_query {
  checkIn?: string
  checkOut?: string
  lang?: string
  webProductId?: string
}

@Component({
  components: {HotelDatePicker, SearchResultRoomAdditional, DetailsInfo, PriceCurrency}
})
export default class SearchResultRoom extends Vue {
  @Prop({type: Object, default: ()=> null}) itemData!: any;
  $store:any=store;
  created() {
    this.searchFilter = {...this.filterSettings}
  }
  swiperOption = {
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev'
    }
  };
  showInfo = false;
  searchFilter:i_filterSettings = {
    checkIn: '',
    checkOut: '',
    checkInDate: null,
    checkOutDate: null,
    nights: 0,
    //roomTypeSelected: [],
    adults: '1',
    child: '0',
    allPersons: 1
  };


  reservationsData:in_reservations = {
    webProductId: this.itemData.id,
    encryptedCompanyId: this.itemData.encryptedCompanyId,
    fromDate: "2020-02-13T17:08:04.133Z",
    toDate: "2020-02-13T17:08:04.133Z",
    priceId: 0,//????
    numberOfPeople: this.filterSettings.allPersons,//????
    pensionType: "withoutBreakfast",//????
    additionalServices: [],
    additionalMerchandises: []//????
  };

  get filterSettings():i_filterSettings {
    return this.$store.state.mod_search.filterSettings
  }
  get additionalTotalPrice() {
    return this.reservationsData.additionalServices.reduce((sum:number, el:any) => {return sum + el.totalPrice}, 0);
  }
  get roomTotalPrice() {
    return this.additionalTotalPrice
  }
  get allPersonsCurrentRoom() {
    return +this.searchFilter.adults + +this.searchFilter.child
  }
  get itemCurrency() {
    return '???'
  }

  // @Watch('searchFilter.adults')
  // @Watch('searchFilter.child')
  // showErrorNotification() {
  //   const txt = (this.allPersonsCurrentRoom > this.itemData.maxPeople || this.allPersonsCurrentRoom < this.itemData.minPeople)
  //     ? `In the ${this.itemData.name}, number of people from ${this.itemData.maxPeople} to ${this.itemData.minPeople}`
  //     : '';
  //
  //   if(!txt) return;
  //
  //   this.$store.dispatch('mod_globalView/SHOW_notification', {
  //     //message: this.$t('page-account_status-update_success'),
  //     message: txt,
  //     type: 'error'
  //   });
  // }

  addAdditional(val:any) { //TODO add i_reservationsItem
    const services = this.reservationsData.additionalServices;
    const index = services.findIndex((el)=> el.id === val.id);
    if(index <= 0) this.reservationsData.additionalServices.push(val);
    else this.reservationsData.additionalServices[index] = val;
  }
  deleteAdditional(val:any) {
    const services = this.reservationsData.additionalServices;
    this.reservationsData.additionalServices = services.filter((el)=> el.id !== val.id);
  }
  toggleInfo() {
    this.showInfo = !this.showInfo
  }
  startBooking() {
    this.addToCart();
    this.$router.push('checkout');
  }
  addToCart() {
    const cartItem:i_cartItem = {
      product: this.itemData,
      reservationsId: 0,
      bookingMeta: {
        nights: this.searchFilter.nights,
        people: this.filterSettings.allPersons,
        checkIn: this.filterSettings.checkIn,
        price: this.roomTotalPrice,
        currency: this.itemCurrency
      }
    };
    this.$store.dispatch('mod_cart/ADD_product', cartItem);
  }
}
