






























import {Component, Vue, Prop, Watch} from 'vue-property-decorator';

import HotelDatePicker  from '@/components/forms/hotel-date-picker.vue';
import BaseCheckbox     from '@/components/forms/base-checkbox.vue';
import {i_checkinPeriod, i_select} from "@/core/declarations";
import {convertToDateFormat}   from '@/core/helpers.ts';
import {Route} from "vue-router";
import {RequestError} from "visbook-api";
import store from "@/store";
interface i_filterParams {
  checkinPeriod: i_checkinPeriod
  webProductIdSelected: i_select[]
  adults: string,
  child: string
}
interface i_query {
  checkIn?: string
  checkOut?: string
  lang?: string
  webProductId?: string
}

@Component({
  components: {HotelDatePicker, BaseCheckbox}
})
export default class SearchFilterDynamic extends Vue {
  $store:any=store;
  created() {
    const query:i_query = this.$route.query;
    this.getRoomTypeList()
      .then(()=> {
        if(query.webProductId) {
          const webProductList = JSON.parse(query.webProductId);
          this.updateRoomType(this.roomTypeList.filter((el) => webProductList.includes(el.value)))
        }
      })
      .catch(()=> {});
  }
  get roomTypeSelected():i_select[] {
    return this.$store.state.mod_search.filterSettingsDynamic.roomType
  }
  get roomTypeList():i_select[] {
    return this.$store.state.mod_search.roomTypeList
  }
  get sortBySelected():i_select[] {
    return this.$store.state.mod_search.filterSettingsDynamic.sortBy
  }
  get sortByList():i_select[] {
    return this.$store.state.mod_search.sortByList
  }
  getRoomTypeList() {
    return this.$store.dispatch('mod_search/GET_roomTypeList')
  }
  updateRoomType(newList:i_select[]) {
    this.$store.dispatch('mod_search/SET_roomTypeSelected', newList)
  }
  updateSortBy(newVal:i_select) {
    this.$store.dispatch('mod_search/SET_sortBySelected', newVal)
  }
}
