




















  import {Component, Prop, Vue} from 'vue-property-decorator';

@Component({})
export default class PaginationPageList extends Vue {
  @Prop({type: Number}) totalItems!: number;
  @Prop({type: Number}) itemsInPage!: number;
  @Prop({type: Number, default: 1}) value!: number;
  @Prop({type: Number, default: 2}) pageRange!: number;

  get pagesList() {
    let pages = [];
    for(var i = this.rangeStart; i <= this.rangeEnd; i++) {
      pages.push(i)
    }
    return pages
  }
  get rangeStart() {
    let start = this.value - this.pageRange;
    return (start > 0) ? start : 1
  }
  get rangeEnd() {
    var end = this.value + this.pageRange;
    return (end < this.totalPages) ? end : this.totalPages
  }
  get totalPages() {
    return Math.ceil(this.totalItems/this.itemsInPage)
  }
  get showFirst() {
    return this.rangeStart > 1
  }
  get showStartDots() {
    return this.rangeStart > 2
  }
  get showLast() {
    return this.rangeEnd < this.totalPages
  }
  get showStopDots() {
    return this.rangeEnd < this.totalPages - 1
  }

  setPage(page:number) {
    this.$emit('input', page)
  }
}
