






























  import {Component, Prop, Vue} from 'vue-property-decorator';
  import PaginationPageList from "@/components/the-pagination/pagination-page-list.vue";
  import SearchFilterDynamic from "@/components/page-search/search-filter-dynamic.vue";

@Component({
  components: {SearchFilterDynamic, PaginationPageList}
})
export default class ThePagination extends Vue {
  @Prop({type: Array, default: ()=> []}) allItems!: any[];
  @Prop({type: Number, default: 5}) itemsInPage!: number;
  @Prop({type: String, default: ''}) messageEmptyList!: string;

  currentPage = 1;

  get currentPageItems() {
    const start = (this.currentPage * this.itemsInPage) - this.itemsInPage;
    const stopPage = this.currentPage * this.itemsInPage;
    const stop = stopPage < this.listLength ? stopPage : this.listLength;
    return this.allItems.slice(start,  stop)
  }
  get listLength() {
    return this.allItems.length
  }
  get showRequestLoader() {
    return this.$store.state.mod_globalView.requestLoader;
  }
}
